.contentContainer {
    color: white;
    position: relative;
    margin-left: 50%;
    transform: translateX(-50%)
}

.contentSubcontainers {
    display: flex;
    width: 100%;
    justify-content: space-between;
}

ul {
    list-style: none;
    margin-left: 0;
    margin-top: 0;
    padding-left: 0;
}

li {
    padding-left: 1em;
    text-indent: -1em;
  }

li:before {
    content: "â–¸";
    padding-right: 3px;
  }

h3 {
    font-size: 26px;
    margin-bottom: 9px;
}

h2 {
    font-size: 30px;
    margin-bottom: 19px;
}

a {
    color:rgb(251, 202, 244);
    text-decoration: none;
}

input {
    font-size: 20px;
    background: none;
    border: none;
    border-bottom: 2px solid white;
    color: white;
    outline: none;
    transition: all .3s linear;
}

input:hover {
    border-bottom: 2px solid rgb(254, 215, 249);
}

input:focus {
    border-bottom: 2px solid rgb(251, 202, 244);
}

label {
    font-style: italic;
    color: rgba(255, 255, 255, 0.46);

}

.formRow {
    display: flex; 
    justify-content: space-between;
    margin-bottom: 10px;
}

.formRowGroup {
    margin-bottom: 30px;
}

.footerContainer {
    margin-bottom: 50px;
    display: flex;
    justify-content: space-between;
}

.navigationContainer {
    color: white;
    position: absolute;
    width: 100%;
    display: flex;
    justify-content: space-around;
    top: 10px;
    left: 50vw;
    transform: translateX(-50%);
    z-index: 2;
}

.navButton {
    color: white; 
    border: none;
    border-bottom: 1px solid white;
    padding: 10px;
    padding-left: 20px;
    padding-right: 20px;
    transition: all 0.2s;
    background-color: rgba(0, 0, 0, 0);
    margin-left: 5px;
    margin-right: 5px;
}
  
.navButton:hover {
    background-color: rgba(255, 255, 255, 0.3);
    cursor: pointer;
}

.navButtonHighlighted {
    color: white; 
    border: none;
    border-bottom: 1px solid white; 
    padding: 10px;
    padding-left: 20px;
    padding-right: 20px;
    transition: all 0.2s;
    background-color: rgba(255, 255, 255, 0.2);
    margin-left: 5px;
    margin-right: 5px;
}
  
.navButtonHighlighted:hover {
    background-color: rgba(255, 255, 255, 0.3);
    color: white;
    cursor: pointer;
}

@media (max-width: 325px) {
    .headerTitle {
        top: 100px; 
    }

    .navButton {
        font-size: 10px;
    }

    .navButtonHighlighted {
        font-size: 10px;
    }
}

@media (max-width: 500px) and (min-width: 325px) {
    .headerTitle {
        top: 100px; 
    }

    .navButton {
        font-size: 14px;
    }

    .navButtonHighlighted {
        font-size: 14px;
    }
}

@media (min-width: 500px) {
    .headerTitle {
        top: 150px; 
    }
    
    .navButton {
        font-size: 20px;
    }

    .navButtonHighlighted {
        font-size: 20px;
    }
}

@media (max-width: 1200px) {
    .headerText {
        color: white;
        font-size: 70px;
        margin: 0;
        font-family: "HeaderFont";
        font-weight: normal;
        line-height: 70px;
    }

    .headerTitle {
        position: absolute; 
        left: 50%; 
        width: 90%;
        text-align: center;
        transform: translateX(-50%); 
        z-index: 2;
    }

    .contentContainer {
        width: 90%;
    }
}

@media (min-width: 1200px) {
    .headerText {
        color: white;
        font-size: 90px;
        margin: 0;
        font-family: "HeaderFont";
        font-weight: normal;
        line-height: 90px;
    }

    .headerTitle {
        position: absolute; 
        left: 50vw; 
        width: 1024px; 
        transform: translateX(-50%); 
        z-index: 2;
    }

    .contentContainer {
        width: 1024px;
    }
}

@media (max-width: 900px) {
    .contentSubcontainers {
        flex-direction: column;
    }

    .contentSubcontainer {
        width: 100%;
    }
}

@media (min-width: 900px) {
    .contentSubcontainers {
        flex-direction: row;
    }

    .contentSubcontainer {
        width: 48%;
    }
}